
import React, { useEffect, useState } from 'react';
import {
    Row,
    Col,
    Card,
    Button,
    Modal,
     InputGroup,
    FormControl,
} from "react-bootstrap";
import { Formik } from "formik";
import * as Yup from "yup";
import Checkbox from '@material-ui/core/Checkbox';
import Aux from "../../../hoc/_Aux";
import CustomInput from "../../../Components/CustomInput";
import MessageModal from "./../../../Components/Alert/Alert";

import axios from 'axios';
import i18n from './../../../i18n';

var urlbackend = require("../../../env/env")();
const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;
const SignupSchema = Yup.object().shape({
    email: Yup.string().email("Invalid Email").required("Email is Required"),
    phone_number: Yup.string()
        .matches(phoneRegExp, "Phone number is not valid")
        .required("Phone is Required"),
    city: Yup.string()
        .min(2, "City is Too Short!")
        .max(20, "City is Too Long!")
        .required("City is Required"),
    country: Yup.string()
        .min(2, "Country is Too Short!")
        .max(20, "Country is Too Long!")
        .required("Country is Required"),
    firstname: Yup.string()
        .min(2, "firstname is Too Short!")
        .max(20, "firstname is Too Long!")
        .required("firstname is Required"),
    lastname: Yup.string()
        .min(2, "lastname is Too Short!")
        .max(20, "lastname is Too Long!")
        .required("lastname is Required"),
});
function UpdateAdmin(props) {
    const [show, setShow] = useState(false);
    const [adminsUpdate] = useState(props.admin)
    const [privileges , setprivileges]=useState([])
    const [agences , setagences]=useState([])
    const [permissions , setpermissions]=useState([])
    const [permissionss]=useState([])
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [showMessageParams, setShowMessageParams] = useState({
        display:false,
        type:"success",
        message:null
    })
    
    useEffect(() => {

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        var getprivileges = axios.get(urlbackend + "/privileges", config)
        var getagence = axios.get(urlbackend + "/agence", config)
        var getpermissions = axios.get(urlbackend + "/permissions", config)


        axios
            .all([getprivileges, getagence, getpermissions])
            .then(
                axios.spread((...response) => {
                    console.log("this respon list0", response[2].data)
                    console.log("this respon list1", response[1].data)
                    //  this.setState({ privileges: response[0].data.data })
                    // this.setState({ agences: response[1].data.data })
                    // this.setState({ permissions: response[2].data.data })
                     setprivileges(response[0].data.data)
                     setagences(response[1].data.data)
                     setpermissions(response[2].data.data)

                })
            )
            .catch(errors => {
                // react on errors.
                console.error(errors);
            });

    }, [])
    const handlSubmit = (values) => {
        setShowMessageParams({
            display:false,
            type:"success",
            message:null
        });
        values.permissions = permissionss
        console.log(values);


        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };

        axios.put(urlbackend + "/user/"+adminsUpdate.id, values, config)
            .then(response => {
                // alert("add Succefull")
                setShowMessageParams({
                    display:true,
                    type:"success",
                    message:i18n.t('success-update-admin')
                });
                console.log(response.data)
                props.change()
                handleClose()
               // window.location.href = "/datta-able/react/default/dashboard/default"


            })
            .catch(e => {
                setShowMessageParams({
                    display:true,
                    type:"danger",
                    message:i18n.t('error-update-admin')
                });
                console.log(e)
            })
    };
    useEffect( () => {
        // console.log("show message value:",showMessage);
    }, [showMessageParams]);
  const  showForm = ({
        values,
        errors,
        touched,
        handleChange,
        handleSubmit,
        handleBlur,
        setFieldValue,
        isSubmitting,
        dirty,
        isValid,
        handleReset,
    }) => {
        // console.log(errors);
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5">{i18n.t('update-admin')}</Card.Title>
                            </Card.Header>
                            <Card.Body>
                            {showMessageParams.display?
                            <MessageModal message={showMessageParams.message} design={showMessageParams.type} showit={showMessageParams.display} />
                            :null}
                                <Row>
                                    <Col md={6}>
                                        <CustomInput
                                            name="firstname"
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.firstname}
                                            errors={errors.firstname}
                                            touched={touched.firstname}
                                            label={i18n.t('first-name')}
                                        />

                                        <CustomInput
                                            name="lastname"
                                            label={i18n.t('last-name')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.lastname}
                                            errors={errors.lastname}
                                            touched={touched.lastname}
                                        />
                                        <CustomInput
                                            name="username"
                                            label={i18n.t('username')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.username}
                                            errors={errors.username}
                                            touched={touched.username}
                                        />

                                        <CustomInput
                                            name="password"
                                            label={i18n.t('password')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.password}
                                            errors={errors.password}
                                            touched={touched.password}
                                            type="password"
                                        />
                                        <CustomInput
                                            name="email"
                                            label={i18n.t('email')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.email}
                                            errors={errors.email}
                                            touched={touched.email}
                                        />

                                        <div>

                                            {permissions.map((e) => (
                                                <div>
                                                    <Checkbox
                                                        onChange={(e) => this.handleChange(e)}
                                                        inputProps={{ 'aria-label': 'primary checkbox' }}
                                                        value={e.id}
                                                        key={e.id}
                                                    />  {e.name} 
                                                </div>
                                            ))}

                                        </div>

                                    </Col>
                                    <Col md={6}>
                                        <CustomInput
                                            name="phone_number"
                                            label={i18n.t('phone-number')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.phone_number}
                                            errors={errors.phone_number}
                                            touched={touched.phone_number}
                                        />
                                        <CustomInput
                                            name="address"
                                            label={i18n.t('address')}
                                            onChange={handleChange}
                                            onBlur={handleBlur}
                                            value={values.address}
                                            errors={errors.address}
                                            touched={touched.address}
                                        />
                                        <label htmlFor="basic-url">{i18n.t('descriminator')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="descriminator"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.Formateur}
                                            >
                                                <option>{i18n.t('choose-descriminator')}</option>

                                                {[
                                                    `regular_user`,
                                                    `magasinier`,
                                                ].map((descriminator, index) => (
                                                    <option key={descriminator} value={descriminator}>
                                                        {descriminator}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>


                                        <label htmlFor="basic-url">{i18n.t('privilege')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="privilege"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.privilege}
                                            >
                                                <option>{i18n.t('choose-privilege')}</option>

                                                {privileges.map((privilege, index) => (
                                                    <option key={privilege.id} value={privilege.id}>
                                                        {privilege.name} :{privilege.description}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>


                                        <label htmlFor="basic-url">{i18n.t('agency')}</label>

                                        <InputGroup className="mb-3">
                                            <FormControl
                                                as="select"
                                                id="agence"
                                                custom
                                                onChange={handleChange}
                                                onBlur={handleBlur}
                                                value={values.agence}
                                            >
                                                <option>{i18n.t('choose-agency')}</option>

                                                {agences.map((agence, index) => (
                                                    <option key={agence.id} value={agence.id}>
                                                        {agence.name} :{agence.address}
                                                    </option>
                                                ))}
                                            </FormControl>
                                        </InputGroup>


                                    </Col>
                                </Row>

                                
                                <Button
                                    onClick={() => {
                                        handlSubmit(values);
                                        handleReset();
                                        // console.log(values);
                                    }}
                                >
                                    {i18n.t('save')}
                </Button>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    };
    return (
        <>
            <Button variant="info"
                onClick={handleShow}> <span className="fa fa-pencil"></span></Button>


            <Modal size="xl" show={show} onHide={handleClose}>
                <Modal.Header closeButton>
                    <Modal.Title>{i18n.t('update-admin')}</Modal.Title>
                </Modal.Header>
                <Modal.Body>

                <Formik
                initialValues={{
                    firstname: `${adminsUpdate.firstname}`,
                    lastname: `${adminsUpdate.lastname}`,
                    username: `${adminsUpdate.username}`,
                    password: `${adminsUpdate.password}`,
                    email:`${adminsUpdate.email}`,
                    phone_number: `${adminsUpdate.phone_number}`,
                    address: `${adminsUpdate.address}`,
                    agence: `${adminsUpdate.agence.id}`,
                    privilege: `${adminsUpdate.privilege}`
                }}
                onSubmit={(values, { setSubmitting }) => {
                    this.submitForm(values);
                    setSubmitting(false);
                }}
                validationSchema={SignupSchema}
            >
                {(props) => showForm(props)}
            </Formik>


                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleClose}>
                    {i18n.t('close')}
            </Button>
                    {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
                </Modal.Footer>
            </Modal>
        </>
    );
}

export default UpdateAdmin