import React from 'react';
import { Row, Col, Card, Table, Button } from 'react-bootstrap';
import { Link } from "react-router-dom"
import axios from 'axios'
import { confirmAlert } from 'react-confirm-alert';
import 'react-confirm-alert/src/react-confirm-alert.css'; // Import
import Aux from "../../../hoc/_Aux";
import ViewAdmin from './ViewAdmin'
import UpdateAdmin from './Update.Admin';
import i18n from './../../../i18n';

var urlbackend = require('../../../env/env')();


class ListAdmin extends React.Component {
    state = {
        Admins: [],
    };

    componentDidMount() {

        const config = {
            headers: {
                'Content-Type': 'application/json',
                'Access-Control-Allow-Headers': 'Authorization',
                "x-access-token": localStorage.getItem('token'),
                'Authorization': 'Bearer ' + localStorage.getItem('token'),
            },
        };
        axios.get(urlbackend + "/users/admin", config)
            .then(response => {
                console.log("this respon list", response.data)
                this.setState({ Admins: response.data.data })

            })
            .catch(e => {
                console.log(e)
            })

    }

    Delette(Admin) {
        console.log("hellodelet", Admin.id)
        confirmAlert({
            title: 'Confirm Delete',
            message: 'Are you sure to do Delete?',
            buttons: [
                {
                    label: 'Yes',
                    onClick: () => {
                        axios.delete("http://localhost:5000/api/v1/Admins/" + Admin.id).then(() => {

                            var array = [...this.state.Admins];
                            var index = array.indexOf(Admin)
                            if (index !== -1) {
                                array.splice(index, 1);
                                this.setState({ Admins: array });
                            }


                        })
                    }
                },
                {
                    label: 'No',
                    onClick: null
                }
            ]
        });
        /*  */
    }
    render() {
        return (
            <Aux>
                <Row>
                    <Col>
                        <Card>
                            <Card.Header>
                                <Card.Title as="h5"> <Link to="/Add/admin" >  <Button>{i18n.t('add-admin')}</Button> </Link> </Card.Title>
                            </Card.Header>
                            <Card.Body>
                                <Table striped responsive>
                                    <thead>
                                        <tr>
                                            <th>{i18n.t('fullname')}</th>
                                            <th>{i18n.t('username')}</th>
                                            <th>{i18n.t('phone-number')}</th>
                                            <th>{i18n.t('agency')}</th>
                                            <th>{i18n.t('address')}</th>
                                            <th>{i18n.t('role')}</th>
                                            <th>{i18n.t('action-event')}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.Admins.map((Admin, index) => (

                                            <tr key={Admin.id}>
                                               <td> {Admin.firstname} {Admin.lastname}</td>
                                                <td>{Admin.username} </td>
                                                <td>{Admin.phone_number} </td>
                                                <td>{Admin.agence.name} </td>
                                                <td>{Admin.agence.address}  {Admin.city} </td>
                                                <td> {Admin.descriminator} </td>
                                                <td>  <Button variant="danger"
                                                    onClick={() => this.Delette(Admin)}> <span className="fa fa-trash"></span></Button>

                                                        <UpdateAdmin admin={Admin} change={ ()=>this.componentDidMount()}/>
                                                        <ViewAdmin admin={Admin} />
                                                </td>
                                            </tr>


                                        ))}
                                    </tbody>
                                </Table>
                            </Card.Body>
                        </Card>
                    </Col>
                </Row>
            </Aux>
        );
    }
}

export default ListAdmin;