
import React, { useState } from 'react';
import { Modal, Button } from 'react-bootstrap';
import i18n from './../../../i18n';

function ViewAdmin(props) {
  const [show, setShow] = useState(false);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  return (
    <>
      <Button variant="success"
        onClick={handleShow}> <span className="fa fa-eye"></span></Button>


      <Modal show={show} onHide={handleClose}>
        <Modal.Header closeButton>
          <Modal.Title>{i18n.t('admin')} : {props.admin.username}  </Modal.Title>
        </Modal.Header>
        <Modal.Body>

        
            <tr>
              <th style={{width: "60%"}}>{i18n.t('fullname')}:</th>
              <td style={{width: "60%"}}>{props.admin.firstname} {props.admin.lastname} </td>
            </tr>
            <tr>
              <th style={{width: "60%"}}>{i18n.t('phone-number')}: </th>
              <td style={{width: "60%"}}>{props.admin.phone_number}</td>
            </tr>
            <tr>
              <th style={{width: "60%"}}>{i18n.t('email')}:</th>
              <td style={{width: "60%"}}>{props.admin.email}</td>
            </tr>
            <tr>
              <th style={{width: "60%"}}>{i18n.t('address')}:</th>
              <td style={{width: "60%"}}>{props.admin.address}</td>
            </tr>
            <tr>
              <th style={{width: "60%"}}>{i18n.t('role')}:</th>
              <td style={{width: "60%"}}>{props.admin.descriminator}</td>
            </tr>
            <tr>
              <th style={{width: "60%"}}>{i18n.t('agency')}:</th>
              <td style={{width: "60%"}}>{props.admin.agence.name}</td>
            </tr>

        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>
          {i18n.t('close')}
            </Button>
          {/* <Button variant="primary" onClick={handleClose}>
            Save Changes
            </Button> */}
        </Modal.Footer>
      </Modal>
    </>
  );
}

export default ViewAdmin